.App {
  text-align: center;
}

.container{
  width: 100%;
  height: 100vh;
  padding: 20px;
  display: flex;
}

.left, .right{
  flex-basis: 50%;
  padding: 10px;
}

iframe{
  width: 100%;
  height: 95%;
  background: #fff;
  border: 0;
  outline: 0;
}

textarea{
  width: 100%;
  height: 28%;
  background: #303d34;
  color: #fff;
  padding: 10px 20px;
  border: 0;
  outline: 0;
  font-size: 18px;
}

label i{
  margin-right: 10ox;
  margin-left: 10px;
}

label{
  display: flex;
  align-items: center;
  background: grey;
  height: 30px;
  width: 100%;
}
.background-container {
  background-image: url("C:\Users\MEHAKDEEP SINGH\Desktop\Online Code Editor\code-editor\src\background.png");
  background-size: cover; /* Optional: Adjust as needed */
  background-repeat: no-repeat; /* Optional: Adjust as needed */
  height: 120vh; /* Optional: Set the height of the container */
}

.highlight{
  color:#fff;
  padding-left: 700px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
